.footerTop {
    padding-top: 44px;
    height: 391px;
    width: 100%;
    font-size: 14px;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #4f181d;
}

.copyright {
    color: #776163;
    font-size: 10px;
    border-top: 1px solid #552c30;
    width: 100%;
    height: 60px;
    background-color: #421418;
    display: flex;
    justify-content: center;
    display: flex;
    align-items: center;
}

.location {
    margin-top: 27px;
    display: flex;
}

.logo {
    /* margin-top: 44px; */
    width: 66px;
    height: 31px;
}

.place {
    width: 15px;
    height: 85px;
    margin-right: 10px;
}

.locationSecondRow {
    margin-top: 5px;
}

.locationFirstRow,
.locationSecondRow {
    height: 40px
}

.facebook,
.instagram {
    display: flex;
    margin: 0 8px;
    margin-top: 27px;
    justify-content: center;
    align-items: center;
    width: 35px;
    height: 30px;
    border: 2px solid #f3820a;
    border-radius: 15%;
}

.footerTabletWrap {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.facebook img,
.instagram img {
    width: 16px;
    height: 16px;
    fill: white;
}

.sns {
    display: flex;
    justify-content: center;
}

.contactUs {
    color: black;
    font-size: 16px;
    font-weight: bold;
    width: 240px;
    height: 56px;
    margin-top: 30px;
    background-color: #f3820a;
    border-radius: 3%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.copy {
    border-right: 1px solid #543335;
}

.copy,
.madeBy {
    height: 20px;
    display: flex;
    align-items: center;
    padding: 10px;
}

.orangeText {
    color: #f3820a;
}

.underlinedText {
    text-decoration: underline #776163;
}


/* TABLET */


@media (min-width: 768px) {
    .footerTabletWrap {
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        margin-right: 0;
    }

    .locationWrapper {
        width: 400px;
        display: flex;

        align-items: center;
    }

    .footerTop {
        align-items: center;
        justify-content: center;
    }

    .footerTop {
        height: 238px;
        display: flex;
        padding-top: 0;
    }

    .locationSecondRow {
        margin-left: 50px;
    }

    .contactUs {
        margin: 0;
    }

    .facebook,
    .instagram {
        margin-top: 0;
    }

    .place {
        padding-top: 15px;
    }

    .locationSecondRow {
        margin-top: 0;
    }

    .contactUs {
        margin: 0 auto;
        margin-top: 20px;
    }
}


/* LAPTOP */


@media (min-width: 1023px) {
    .footerTabletWrap {
        width: 65%;
    }

    .footerTop {
        height: 171px;
        align-items: center;
        justify-content: center;
    }

    .contactUs {
        margin: 0;
    }

    .logo {
        height: 85px;
        height: 39px;
    }

    .footerLaptopWrap {
        display: flex;
        align-items: center;
        justify-content: space-around;
    }

    .locationWrapper {
        width: 385px;
    }

    .contactUs {
        margin-top: 0;
    }

    .footerLaptopWrap {
        width: 100%;
    }
}


/* DESKTOP */


@media (min-width: 1281px) {
    .logo {
        width: 112px;
        height: 53px;
    }

    .footerLaptopWrap {
        display: flex;
        flex-direction: row;
        justify-content: center;
    }

    .footerTabletWrap {
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
    }

    .footerTabletWrap {
        width: 65%;
    }
}